.advantages {
  background-color: #fff5f6;
  padding: 39px 0 48px;
}

.advantages__value-first::before {
  content: '';
  margin-bottom: 40px;
  display: block;
  width: 33px;
  height: 50px;
  background-image: url(../images/desktop/advantages/icon1.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;

  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url(../images/desktop/advantages/icon1@2x.png);
  }
}
.advantages__value-second::before {
  content: '';
  margin-bottom: 40px;
  display: block;
  width: 50px;
  height: 50px;
  background-image: url(../images/desktop/advantages/icon2.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;

  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url(../images/desktop/advantages/icon2@2x.png);
  }
}
.advantages__value-third::before {
  content: '';
  margin-bottom: 40px;
  display: block;
  width: 44px;
  height: 50px;
  background-image: url(../images/desktop/advantages/icon3.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;

  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url(../images/desktop/advantages/icon3@2x.png);
  }
}

.advantages_value {
  font-family: Titan One, cursive;
  font-weight: 400;
  font-size: 36px;
  line-height: 1.14;
  color: var(--accent-color);
}

.advantages__description {
  font-family: DM Sans, sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.75;
  color: var(--black-color);
  margin-top: 16px;
}

@media screen and (max-width: 767px) {
  .advantages__item:not(:last-child) {
    margin-bottom: 38px;
  }

  .advantages__value {
    font-family: Titan One, cursive;
    font-weight: 400;
    font-size: 36px;
    line-height: 1.14;
    color: var(--accent-color);
  }
}

@media screen and (min-width: 768px) {
  .advantages {
    padding: 66px 0 70px;
  }

  .advantages__list {
    display: flex;
  }

  .advantages__item {
    flex-basis: calc((100% - 3 * 60px) / 3);
    margin-left: 60px;
  }

  .advantages__wrapper-icons:not(:last-child) {
    margin-bottom: 36px;
  }

  .advantages__value {
    font-family: Titan One, cursive;
    font-weight: 400;
    font-size: 48px;
    line-height: 1.15;
    color: var(--accent-color);
  }

  .advantages__description {
    font-size: 14px;
    line-height: 1.74;
  }
}

@media screen and (min-width: 1280px) {
  .advantages {
    padding: 39px 0 69px;
  }

  .advantages__item {
    flex-basis: calc((100% - 3 * 30px) / 3);
    margin-left: 30px;
  }

  .advantages__wrapper-icons:not(:last-child) {
    margin-bottom: 40px;
  }

  .advantages__value {
    font-family: Titan One, cursive;
    font-weight: 400;
    font-size: 58px;
    line-height: 1.14;
    color: var(--accent-color);
  }

  .advantages__description {
    font-size: 16px;
    line-height: 1.76;
  }
}
